//navigation
.navigation {
	position: relative;
	// z-index: 2;
	// height: 48px;
	line-height: 48px;
	background-color: #FCFCFC;
	border-top: 1px solid #E5E5E5;
	border-bottom: 1px solid #E5E5E5;
	@include breakpoint($lg){ 
		.gnb-stiky & {
			width: 100%;
			position: sticky;
			top: -2px;
			z-index: 10;
			// &::before {
			// 	content: "";
			// 	display: block;
			// 	width: 100%;
			// 	height: 2px;
			// 	background-color: $black;
			// 	position: absolute;
			// 	bottom: -1px;
			// 	left: 0;	
			// }
			.sub-_visual {
				display: none;
			}
		}
	}
	// .container {
	// 	@include breakpoint($smd) {
	// 		margin-left: 0;
	// 		margin-right: 0;
	// 	}
	// }
	.sub-_visual {
		// display: none;
		background-image: none;
		border-top: 1px solid #E6E6E6;
		background: #fff;
		@include breakpoint($lg){
			height: 216px;
			background-color: #CBDEF4;
			background-image: url(/static/imer/img/visual/sub_visual_bg1.png);
			background-repeat: no-repeat;
			background-position: 50%;
			&.menu_500214 {
				background-image: url(/static/imer/img/visual/bg_500214.png);
			}		
			&.menu_500216 {
				background-image: url(/static/imer/img/visual/bg_500216.png);
			}		
			&.menu_500536 {
				background-image: url(/static/imer/img/visual/bg_500536.png);
			}	
			&.menu_500045 {
				background-image: url(/static/imer/img/visual/bg_500045.png);
			}	
			&.menu_500218 {
				background-image: url(/static/imer/img/visual/bg_500218.png);
			}	
			&.menu_500831 {
				background-image: url(/static/imer/img/visual/bg_500831.png);
			}	
		}
		@include breakpoint($smd) {
			border-bottom: 2px solid #1a1a1a;
		}
		div {
			width: $pc-wide;
			max-width: 100%;
			margin: 0 auto;
			//padding: 20px 10px 0;
			@include breakpoint($lg){
				padding-top: 90px;
			}
			@include breakpoint($smd){
				padding: 30px 0;
			}
			h1 {
				// color: $black;
				color: #000E33;
				font-weight: 700;
				font-size: rem(48);
				@include breakpoint($smd){
					font-size: rem(36);
					text-align: center;
				}
				.bd-body & {
					font-size: rem(20);
				}
			}
		}
	}
	.top-page-toolbar {
		// display: none;
		.nav-stiky & {
			// display: block;
			float: right;
		}
		.page-toolbar {
			border: 1px solid #E5E5E5;
			border-width: 0 1px;
			a,
			button {
				&::before {
					font-size: 30px;
					margin-bottom: -4px;
				}
			}
			.sns-toggle {
				border: none;
			}
			.page-print {
				background-size: 30px 30px;
				border: 1px solid #E5E5E5;
				border-width: 0 1px;
				margin: 0;
			}
			.page-rss {
				&::before {
					margin-left: 16px;
				}
			}
		}
	}
	// @include breakpoint($sm){
	// 	height: 36px;
	// 	line-height: 36px;
	// }
	nav {
		height: 60px;
		line-height: 60px;		
		@include breakpoint($lg){
			display: flex;
			justify-content: space-between;
		}
		@include breakpoint($smd) {
			height: 36px;
			line-height: 36px;
			margin: 0 auto;
			border-bottom: 1px solid #E5E5E5;
		}
		> ul {
			display: flex;
			border-left: 1px solid #E5E5E5;
			> li {
				position: relative;
				border-right: 1px solid #E5E5E5;
				> a {
					display: block;	
					padding: 0 10px;
					color: #6D6E72;
					@include breakpoint($lg){
						padding: 0 26px;
					}
					&::after {
						@include font-ico-outline('\e5c6');
						color: #6D6E72;
						font-size: 22px;
						margin-left: 10px;
						@include breakpoint($sm){
							position: absolute;
							top: 50%;
							right: 8px;
							margin-top: -11px;
						}
					}
					
				}
				&:last-of-type {
					> a {
						color: #000E33;
						text-decoration: underline;
						&::after {
							content: "";
							display: inline-block;
							width: 24px;
							height: 24px;
							transform: translateY(2px);
							background: url('/static/museum/img/ico/ico-lnb-last.png');
							@include breakpoint($sm){ 
								transform: translateY(0);
							}
						}
					}
				}
				@include breakpoint($sm){
					width: 100%;
					&:last-child {
						border-right: none;
					}
				}
				&.home {
					// color: #000;
					color: #6D6E72;
					padding: 0 16px;
					@include breakpoint($lg){
						padding: 0 26px;
					}
					@include breakpoint($smd){
						width: 36px;
						text-align: center;
						span {
							display: none;
						}
					}
					&::before {
						content: "";
						display: inline-block;
						width: 18px;
						height: 18px;
						background: url(/static/portal/img/ico/ico-home.svg) 50% no-repeat;
						//@include font-ico('\e88a');
						//vertical-align: bottom;
						//font-size: 26px;
						//color: $point;
						margin-right: 6px;
						margin-bottom: -2px;
						@include breakpoint($smd){
							position: absolute;
							top: 0;
							left: 0;
							z-index: 9;
							text-align: center;
							width: 36px;
							height: 100%;
							margin-right: 0;
						}
						// @include breakpoint($sm) {
						// 	top: 0;
						// 	height: 48px;
						// }
					}	
				}
				// > a {
				// 	display: block;	
				// 	padding: 0 10px;
				// 	@include breakpoint($lg){
				// 		padding: 0 26px;
				// 	}
				// 	&::after {
				// 		@include font-ico-outline('\e5c6');
				// 		color: #666;
				// 		font-size: 22px;
				// 		margin-left: 10px;
				// 		@include breakpoint($sm){
				// 			position: absolute;
				// 			top: 50%;
				// 			right: 8px;
				// 			margin-top: -11px;
				// 		}
				// 	}
				// }
				.depth2 {
					display: none;
					position: absolute;
					top: 60px;
					left: -1px;
					min-width: 200px;
					z-index: 9;
					width: calc(100% + 2px);
					background-color: #fff;
					@include breakpoint($sm){
						top: 36px;
					}
					ul {
						width: 100%;
						padding: 10px 15px 0; 
						line-height: 1.8;
						border: 1px solid #E5E5E5;
						border-radius: 0 0 4px 4px;
						box-shadow: 0 4px 2px rgba(127, 24, 24, 0.15);
						li {
							margin: 10px 0;
							a {
								color: #666;
								display: block;
								line-height: 1.3;
								&:hover {
									text-decoration: underline;
								}	
							}
							&.on {
								a {
									color: $black;
									font-weight: 600;
									&::after {
										@include font-ico-outline('\e5cc');
										font-size: 18px;
										margin-bottom: 2px;
									}
								}
							}
						}
					}
					&.active {
						display: block;
					}
				}
			}
		}
	}
}

@include breakpoint($sm) {
	.navigation nav > ul > li:not(.home) {
		display: none;
	}
	.navigation nav > ul > li:last-of-type{
		display: block;
	}
}

/* 상세페이지 비쥬얼 */
body.bd-body .sub-_visual {
	@include breakpoint($lg) {
		&.menu_500216 {
			background-image: url(/static/imer/img/visual/view_500216.png);
		}
		&.menu_500831 {
			background-image: url(/static/imer/img/visual/view_500831.png);
		}
	}
}

@include breakpoint($smd) {
	.bd-body {
		.navigation {
			.sub-_visual {
				width: calc(100% - 20px);
				padding: 16px;
				margin: 0 auto;
				background-color: #CBDEF4;
				div {
					padding: 0; 
					h1 {
						text-align: left;
					}
				}
			}
		}
	}
}





//목차형
.lnb-nav{
	position: sticky;
	top: 48px;
	padding-top: 0;
	// &.sticky {
	// 	position: sticky;
	// 	top: 78px;
	// 	padding-top: 0;
	// }
	@include breakpoint($lg){
		//position: relative;
		display: block;
		float: left;
		// width: per(240px + 35px, $pc-wide);
		width: 240px;
		padding-right: 36px;
		background-color: #fff;
		height: auto;
		&::after {
			content: "";
			display: inline-block;
			background-color: #d9d9d9;
			position: absolute;
			top: 0;
			right: -1px;
			width: 1px;
			height: 100%;
			display: none;
		}
	}
	// @include breakpoint($smd){ 
	// 	order: 2;
	// }
	h1 {
		padding-bottom: 6px;
		font-size: rem(16);
		font-weight: 500;
		color: #222;
		&::before {
			@include font-ico-outline('\e0ee');
			font-size: 24px;
			color: #1C1B1F;
			margin-right: 5px;
		}
	}	
	.lnb  {
		@include breakpoint($smd) {
			display: none;
		}
		ul {
			> li {
				> a {
					display: block;
					padding: 6px 0;
					color: #333;
				}
				&.on {
					> a {
						display: flex;
						font-weight: 500;
						align-items: center;
						justify-content: space-between;
						color: $black;
					}
				}
				> ul {
					li {
						a {
							display: block;
							padding: 4px 14px;
							color: #666;
							font-size: rem(14);
						}
						&.on {
							a {
								position: relative;
								color: #2F4F9D;
								font-weight: 500;
								background-color: #F2F6FA;
								border-radius: 4px;
								&::after {
									@include font-ico('\e315');
									font-size: 18px;
									color: #2F4F9D;
									position: absolute;
									top: 2px;
									right: 8px;
								}
							}
						}
					}
				}
			}
		}
	}
}
.lnb + .content {
	border-left: 1px solid #d9d9d9;
}

